    height: 16px;
    cursor: pointer;
    background: none;
    padding: 0;
    box-shadow: none; }
    .Header__burgerMenu.active span::before {
      transition: all 0.5s;
      transform: rotate(135deg); }
    .Header__burgerMenu.active span::after {
      transition: all 0.5s;
      top: -12px;
      position: relative;
      transform: rotate(-135deg); }
    .Header__burgerMenu span {
      transition: all 0.5s;
      display: inline-block;
      width: 100%;
      height: 2px;
      background: black;
      border-radius: 1px;
      margin-bottom: 5px; }
      .Header__burgerMenu span::after {
        content: "";
        transition: all 0.5s;
        display: inline-block;
        width: 100%;
        height: 2px;
        background: black;
        margin-bottom: 5px;
        border-radius: 1px; }
      .Header__burgerMenu span::before {
        transition: all 0.5s;
        content: "";
        display: inline-block;
        width: 100%;
        height: 2px;
        background: black;
        border-radius: 1px; }
